
import { socket, connect } from "@/components/socket/model/Socket";
import { defineComponent } from "vue";
import PhoneInfo from "@/components/monitor/phone/model/PhoneInfo";
import MonitorPhoneScreen from "@/components/monitor/phone/MonitorPhoneScreen.vue";
import axios from "axios";

export default defineComponent ({
  name: "MonitorPhoneCards",

  components: {
    MonitorPhoneScreen
  },

  props: {
  },

  data() {
    return {
      phone_infos: new Map<string, PhoneInfo>() as Map<string, PhoneInfo>,
      runPhoneActionParameters: {
        udid: '',
        actionSeq: 11
      },
    }
  },

  async created() {
    connect()
    this.phones_connect()
    this.phone_infos_connect()
  },

  methods: {

    // 핸드폰 리스트 room join
    phones_connect() {
      socket.emit('join_room', 'phone_infos');
    },

    // 핸드폰 리스트 정보 소켓 수신
    phone_infos_connect() {
      socket.on('phone_infos_connect', (data: PhoneInfo) => {
        this.phone_infos.set(data.udid, data)
      })
    },

    // 핸드폰 wifi 를 통한 adb 연결
    phone_connect_wifi(udid: string) {
      axios.get('http://127.0.0.1:8082/api/monitor/phone/connect/wifi?udid=' + udid)
          .then(res => {
            if (this.phone_infos.has(udid)) {
              const phone_info = this.phone_infos.get(udid)
              if (phone_info == undefined) {
                return
              }
              phone_info.message = res.data.message
              phone_info.message_success = res.data.success
            }
          })
          .catch(error => (console.log(error)))
    },

    // 핸드폰 프록시 지정
    phone_set_proxy(udid: string) {
      axios.get('http://127.0.0.1:8082/api/monitor/phone/set/proxy?udid=' + udid)
          .then(res => {return})
          .catch(error => (console.log(error)))
    },

    // 핸드폰 메인 action loop 실행
    loop_main_action(udid: string) {
      this.runPhoneActionParameters.udid = udid

      axios.get('http://127.0.0.1:8082/api/automation/phone/action/appium/loop', {params : this.runPhoneActionParameters})
          .then(res => {console.log(res)})
          .catch(error => (console.log(error)))
    },
  }
})
